<template>
    <form @submit.prevent="search">
        <b-input v-model="query"
          type="search"
          icon="search"
          autocomplete="off"
          autocorrect="off"
          autocapitalize="off"
          spellcheck="false">
        </b-input>
    </form>
</template>

<script>
const searchEngine = 'https://duckduckgo.com/?q=';

export default {
  data() {
    return {
      query: '',
    };
  },
  methods: {
    search() {
      window.location.href = `${searchEngine}${this.query}`;
    },
  },
};
</script>



